@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@mixin f {
  font-family: "Karla", Helvetica, Arial, sans-serif; }

body {
  @include f;
  font-size: 14px;
  line-height: 1.3; }
button,
input {
  @include f; }
a {
  color: white;
  transition: opacity .2s;
  &:hover {
    opacity: .7; } }

.page {
  display: flex;
  min-height: 100vh;
  padding: 36px 15px 10px;
  background: url(../img/bg-sign-up.jpg) no-repeat 50% 50% / cover;
  flex-direction: column;
  color: white;
  @include m {
    padding: 64px 10px 10px; }
  &_login {
    background-image: url(../img/bg-sign-in.jpg);
    .form {
      background: black; } } }

.form {
  width: 100%;
  max-width: 432px;
  margin: auto;
  background: #005450;
  box-shadow: 0 16px 22px -10px rgba(0,0,0,0.10), 0 34px 55px 4px rgba(0,0,0,0.05), 0 13px 66px 12px rgba(0,0,0,0.07);
  border-radius: 12px;
  &__container {
    padding: 52px 36px 38px;
    text-align: center;
    @include m {
      padding: 52px 24px 38px; } }
  &__footer {
    padding: 19px 36px;
    border-top: 1px solid rgba(white,.2);
    font-size: 18px;
    text-align: center; } }

.logo {
  margin-bottom: 32px; }

.fieldset {
  margin-bottom: 48px; }

.input {
  width: 100%;
  height: 64px;
  background: none;
  border-bottom: 1px solid rgba(white,.2);
  font-size: 22px;
  color: white;
  text-align: center;
  &:-webkit-autofill {
    -webkit-animation-name: autofill;
    -webkit-animation-fill-mode: both; }
  &::placeholder {
    opacity: .7;
    color: white; } }

@keyframes autofill {
  to {
    color: white;
    background: transparent; } }

.btn {
  min-width: 234px;
  height: 50px;
  background: white;
  border-radius: 25px;
  font-size: 20px;
  font-weight: 700;
  color: #005450;
  letter-spacing: -0.3px;
  transition: opacity .2s;
  &:hover {
    opacity: .7; } }

.text {
  margin-top: 24px;
  opacity: .7;
  font-size: 16px;
  line-height: 1.375; }

.about {
  margin-top: 8px;
  padding: 0 15px;
  text-align: right;
  @include m {
    margin-top: 32px;
    text-align: center; }
  a {
    font-weight: 700; } }








